@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Crafty+Girls);
/* Site wide CSS rules */
html, header, footer, main, body, div, span, h1, h2, h3, h4, h5, h6, p, a, img,
ul, li, form, label, input, button {
  margin: 0;
  padding: 0;
  outline: 0;
  border: none;
}

body {
  font-family: "Crafty Girls", sans-serif;
  background-color: #67AB9F;
  line-height: 1.2;
}

ul {
  list-style: none;
}

input {
  font-family: "Crafty Girls", sans-serif;
  display: inline-block;
  border-bottom: 1px solid gray;
  min-height: 30px;
  height: 8vh;
  font-size: 6vmin;
}

input::-webkit-input-placeholder {
  color: gray;
}

input:-ms-input-placeholder {
  color: gray;
}

input::placeholder {
  color: gray;
}

button {
  background-color: #FFF;
  border: 1px solid black;
  width: 3vmax;
  font-size: 70%;
}

button:hover {
  background-color: gray;
}

.App {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 99%;
  margin: 0.5vh 0 0 0;
}

header h1 {
  font-size: 5vmax;
}

.instructions {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  height: 50%;
}

main {
  -webkit-flex: 1 1;
          flex: 1 1;
  min-width: 99%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.container {
  position: relative;
  width: 65%;
  padding-top: 39%;
}

.flashcard {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #FFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: -1px 1px rgba(0, 0, 0, 0.1), -1px 1px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1);
}

footer {
  padding: 1%;
  font-size: 2.5vh;
}

@media screen {
  .App {
    position: relative;
    overflow: hidden;
  }
}
@media (max-width: 800px) {
  header {
    height: 25vh;
  }

  header h1 {
    font-size: 8vmax;
  }

  .container {
    width: 80%;
    padding-top: 48%;
  }
}

.TopicList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width: 100%;
  height: 100%;
}

.list-wrapper {
  width: 95%;
  min-height: 45%;
  max-height: 45%;
}

.input-wrapper {
  width: 95%;
}

.TopicList input {
  width: 100%;
  height: auto;
}

input::-webkit-input-placeholder {
  font-style: italic;
}

input:-ms-input-placeholder {
  font-style: italic;
}

input::placeholder {
  font-style: italic;
}

.TopicList ul {
  background-color: #FFF;
  width: 99%;
  max-height: 100%;
  font-size: 4vmin;
  max-height: 200px;
  text-indent: 1%;
  border: 1px solid black;
  border-top: none;
  overflow: hidden;
  overflow-y: scroll;
}

.TopicList li:hover {
  background-color: lightgray;
}

.TopicList ul .selected {
  background-color: lightgray;
}

/* Testing */
/*.TopicList { border: 1px solid black; }*/
/*.TopicList input { border: 1px solid black; }*/

.TopicPickerContainer {
  width: 100%;
  height: 100%;
}

.SentencesContainer {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.options {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.options button {
  width: auto;
  padding: 0.2vh 0.3vw;
  font-size: 1.5vmax;
}

.top {
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 0.5vh;
}

.top button {
  border: none;
}

.topic {
  font-size: 4vmin;
}

.back {
  margin-right: auto;
}

.empty {
  color: #FFF;
  margin-left: auto;
}

.help {
  color: darkred;
}

.check {
  color: green;
}

.restart {
  color: darkblue;
}

.Sentence {
  width: 95%;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Sentence p:first-child::first-letter {
  text-transform: capitalize;
}

.Sentence p {
  max-width: 100%;
}

.Sentence p, .Sentence input {
  font-size: 4.5vmin;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.Sentence span, .Sentence input {
  margin: 0.2vh 0.5vw;
}

.Sentence input {
  border-bottom: 1px solid black;
  height: 4vmax;
  font-weight: bold;
  font-style: italic;
  text-indent: none;
}

.correct {
  color: green;
}

.incorrect {
  color: red;
}

