.SentencesContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.options {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.options button {
  width: auto;
  padding: 0.2vh 0.3vw;
  font-size: 1.5vmax;
}

.top {
  justify-content: center;
  margin-top: 0.5vh;
}

.top button {
  border: none;
}

.topic {
  font-size: 4vmin;
}

.back {
  margin-right: auto;
}

.empty {
  color: #FFF;
  margin-left: auto;
}

.help {
  color: darkred;
}

.check {
  color: green;
}

.restart {
  color: darkblue;
}
