.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 99%;
  margin: 0.5vh 0 0 0;
}

header h1 {
  font-size: 5vmax;
}

.instructions {
  align-self: flex-end;
  height: 50%;
}

main {
  flex: 1;
  min-width: 99%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  position: relative;
  width: 65%;
  padding-top: 39%;
}

.flashcard {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #FFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: -1px 1px rgba(0, 0, 0, 0.1), -1px 1px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1), -2px 2px rgba(0, 0, 0, 0.1);
}

footer {
  padding: 1%;
  font-size: 2.5vh;
}

@media screen {
  .App {
    position: relative;
    overflow: hidden;
  }
}
@media (max-width: 800px) {
  header {
    height: 25vh;
  }

  header h1 {
    font-size: 8vmax;
  }

  .container {
    width: 80%;
    padding-top: 48%;
  }
}
